.gallery-container {
  background-color: rgb(239, 241, 246);
  padding: 7em 0em;
  .gallery-header {
    display: grid;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 1em;
    margin-bottom: 7em;
    .header-sep {
      width: 13em;
      height: 3px;
      background: rgb(0, 58, 106);
      text-align: center;
      margin: auto;
    }
    .header-top {
      font-size: 4.5em;
      font-weight: 300;
      color: #878a94;
    }

    .header-top span {
      font-weight: 500;
      color: #3c4251;
    }
    .header-bottom {
      font-weight: 100;
      color: #3c4251;
      font-size: 1.5em;
    }
  }
}
