.slide-container {
  display: flex;
  overflow-x: scroll;
  div::--webkit-scrollbar {
    display: none;
  }
  img::--webkit-scrollbar {
    display: none;
  }
}
.slide-col {
  cursor: pointer;
  width: 361px;
  height: 361px;
  background-color: #eff1f6;
  //   padding: 5px;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: 356px;
    height: 356px;
  }
  img:hover {
    transform: scale(1.1);
    overflow: auto;
  }
}
.buttons {
  display: flex;
  justify-content: center;
}
// modal styles
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 10000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  padding: 20px;
  max-width: 800px;
  max-height: 80%;
  overflow-y: auto;
  text-align: center;
}

.backdrop.hidden {
  display: none;
}

.backdrop img {
  width: 100%;
  max-width: 100%;
  max-height: 80%;
  object-fit: contain;
}

.backdrop::after {
  content: "";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.75);
  z-index: -1;
  backdrop-filter: blur(10px);
}
.image-main {
  display: flex;
  width: 100%;
  .image-container {
    position: relative;
    width: 20%;
    height: 20em;
    &:hover {
      img {
        background: black;
        filter: brightness(50%);
        transform: scale(1.1);
      }
      svg {
        display: block;
        background: #003a6a;
        padding: 10px;
        font-size: 60px;
        position: absolute;
        top: 50%;
        left: 50%;
        color: white;
        transform: translate(-50%, -50%);
      }
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.5s ease all;
    }
    svg {
      display: none;
      position: absolute;
      top: 50%;
      left: 50%;
      transition: 0.5s ease-in-out;
    }
  }
}
